header, footer{
  background: #E0E0E0;
  background: -moz-linear-gradient(top, #ff4411 0%, #E0E0E0 100%);
  background: -webkit-linear-gradient( #ff4411 0%, #E0E0E0 100%);
  background: -o-linear-gradient( #ff4411 0%, #E0E0E0 100%);
  background: linear-gradient( #ff4411 0%, #E0E0E0 100%);
  text-align: center;
  padding: 30px;
  margin: 0px;
}
footer{
  background: linear-gradient( #E0E0E0 0%, #ff4411 100%);
}

nav{
  font-variant: small-caps;
  font-size: 150%;
  font-family: Arial, 'Times New Roman', Times, serif;
}
nav a{
  background-color: #ffffff;
  font-family: Arial, 'Times New Roman', Times, serif;
  float: right;
}
body{
  background: #E0E0E0;
  font-size: 125%;
}
h1{
    color: #ff4411;
    font-family: 'Raleway',sans-serif; 
    font-size: 62px; 
    font-weight: 800; 
    line-height: 72px; 
    margin: 10 0 35px; 
    text-align: center; 
    text-transform: uppercase;
}
.array-container{
    position: relative;
    clear: both;
}

.array-bar{
    width: 8px;
    background-color: turquoise;
    display: inline-block;
    margin: 0 1px;
}
ul {
    list-style-type: none;
    float: left;
    width: 40%;
    margin: 0 0 0 5px;
    padding: 0px;
  }

  li button {
    background-color: #324aa8;
    border-radius: 25px;
    margin: 3px 0px 0px 20px;
    display: block;
    color: #ffffff;
    padding: 18px 20px 18px 20px;
    text-decoration: none;
    font-size: 16px;
    width: 40%;
    border: none;
    text-align: left;
  }
  li button:hover {
    background-color: #ec42f5;
    color: white;
  }
